var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"post",attrs:{"id":"kt_post"}},[_vm._ssrNode("<div class=\"mb-10\"><div class=\"mb-0\"><div class=\"d-flex align-items-center mb-4\"><h1 class=\"fs-2 fw-bolder text-gray-900 text-hover-primary me-1\">\n          Kobi Cari Hesap nasıl çalışır?\n        </h1></div> <p>\n        Kobiye ait oluşturulan Cari Hesaplar burada listelenir. Sol üstte\n        tablo adı, altında Detaylı Arama, Sayfalama, Çıktı Al, Ara, Ekleme,\n        Yardım gibi menüler bulunur. Veri oluşturulduğunda üstüne tıklanıp\n        detaylı alt menüsüne gidilebilir. Sağ üstten &quot;Ekle&quot; butonu ile ekleme\n        yapılabilir.\n      </p> <p>\n        Ekleme menüsünde; Cari Hesap Adı, TCKN / VKN, Grubu, Genel Vade(Gün),\n        Vergi Dairesi, Ticaret Sicil Numarası, Meslek Odası, Telefon ve Etiket\n        kolonları bulunur. Zorunlu alanlar\n        <span class=\"text-danger\">*</span> ibaresiyle gösterilmiştir. Burada\n        &quot;Cari Hesap Adı&quot; ve &quot;TCKN / VKN&quot; zorunlu tutulmuştur. Ayrıca TCKN /\n        VKN kolonu 11 hane girildiği taktirde &quot;Adı&quot; ve &quot;Soyadı&quot; kolonları\n        aktif olur.\n      </p> <p>\n        Carinin detayına gidildiğinde bizi böyle bir sayfa karşılar. Sol üstte\n        ilgili carinin bilgileri yer alır. Hemen sağında ilgili cariye eklenen\n        notlar ve ekleme butonu yer alır. Altında &quot;Satış Yap&quot; ve &quot;Satın Al&quot;\n        butonları bulunurken sağında ilgili cariye ait Borç, Alacak ve Bakiye\n        göstergeleri bulunur. Ayrıca 4 panele ayrılmıştır. Bunlar, Hesap\n        Bakiye Özeti, Sattıklarım, Aldıklarım ve Düzenle panelleridir. Hesap\n        Bakiye Özeti alınan ödemelerin tamamının listelendiği bölümdür.\n      </p> <p>\n        Sattıklarım menüsünde ilgili cariye eklenen satılan ürün siparişleri\n        gösterilir. Buradan ürünlerin Vergili Ödenecek Tutarı ile Ödenmiş\n        Tutar eşit değilse &quot;Hızlı İşlem&quot; altında &quot;Ödeme Butonu&quot; bulunur.\n        Taksit bilgisi girildiyse &quot;Ödeme Butonu&quot; yerine &quot;Taksit&quot; butonu\n        bulunur. Tıklandığında &quot;Bankadan&quot; ya da &quot;Kasadan&quot; ödeme butonları\n        çıkar. Seçildiğinde ise bir sorgu yapılır ve eminseniz işlem\n        tamamlanır. Örneğin 3 taksit bilgisi girildiyse &quot;1. Taksit&quot; yazar. İlk\n        taksit ödendiğinde &quot;2. Taksit&quot; yazar. Tüm taksitler ödendiğinde işlem\n        tamamlanır. Ödeme yapıldığı taktirde Kalan Tutardan düşülür. Tamamı\n        ödenirse eğer Kalan Tutar sıfırlanır ve Ödeme butonu kaldırılır.\n      </p> <p>\n        Satış Yap butonuna tıklandığında bir sipariş ekleme menüsü açılır.\n        Ekleme menüsünde; Ürün Adı, Tutar, KDV, KDV Tutarı, KDV Dahil, Vergili\n        Ödenecek Tutar, Taksit Sayısı, Taksit Tutarı, Vade Tarihi ve Ödenen\n        Tutar kolonları bulunur. Taksit sayısı girilirse &quot;Vergili Ödenecek\n        Tutar&quot; ilgili taksit sayısına bölünerek Taksit Tutarı kolonunda\n        hesaplanır. Ayrıca Ödenen Tutar girilirse altında &quot;Bankadan Ödeme&quot; ve\n        &quot;Kasadan Ödeme&quot; butonları aktif olur. Ekle dediğimizde Kobi Sipariş\n        tablosuna satılan ürün eklenmiş olur.\n      </p> <p>\n        Aldıklarım menüsünde ise ilgili cariye eklenen alınan ürün siparişleri\n        gösterilir. Buradan ürünlerin Vergili Ödenecek Tutarı ile Ödenmiş\n        Tutar eşit değilse &quot;Hızlı İşlem&quot; altında &quot;Ödeme Butonu&quot; bulunur. Ödeme\n        yapıldığı taktirde Kalan Tutardan düşülür. Tamamı ödenirse eğer Kalan\n        Tutar sıfırlanır ve Ödeme butonu kaldırılır. Sattıklarım ile benzer\n        şekilde çalışır.\n      </p> <p>\n        Satın Al butonuna tıklandığında bir sipariş ekleme menüsü açılır.\n        Ekleme menüsünde; Ürün Adı, Tutar, KDV, KDV Tutarı, KDV Dahil, Vergili\n        Ödenecek Tutar, Taksit Sayısı, Taksit Tutarı, Vade Tarihi ve Ödenen\n        Tutar kolonları bulunur. Taksit sayısı girilirse &quot;Vergili Ödenecek\n        Tutar&quot; ilgili taksit sayısına bölünerek Taksit Tutarı kolonunda\n        hesaplanır. Ayrıca Ödenen Tutar girilirse altında &quot;Bankadan Ödeme&quot; ve\n        &quot;Kasadan Ödeme&quot; butonları aktif olur. Ekle dediğimizde Kobi Sipariş\n        tablosuna alınan ürün eklenmiş olur.\n      </p> <p>\n        Düzenle menüsünden ise ilgili cari bilgileri düzenlenebilir. Ayrıca\n        Eklenen notlarda bulunur. Not ekleme modalı şu şekilde gözükür.\n      </p></div> <div class=\"separator separator-dashed my-8\"></div></div> "),_vm._ssrNode("<div class=\"d-flex justify-content-between mb-0\">","</div>",[_c('router-link',{staticClass:"btn btn-light btn-active-light-primary fw-bold fs-6 mx-2",attrs:{"to":"/docs/giris/dogrulama"}},[_vm._v("\n      Önceki")]),_vm._ssrNode(" "),_c('router-link',{staticClass:"btn btn-light btn-active-light-primary fw-bold fs-6 mx-2",attrs:{"to":"/docs/finans-yonetimi/kartlar"}},[_vm._v("Sonraki")])],2)],2)
